import * as React from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"
import NumberFinder from '../components/NumberFinder'
import { Form, Button, InputGroup, Row, Col, Alert } from '@themesberg/react-bootstrap';
import Header from '../components/Head'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch, faEnvelope, faBell, faLayerGroup } from "@fortawesome/free-solid-svg-icons";
import { navigate } from 'gatsby';
import Parse from '../vendor/Parse'


const IndexPage = () => {
  const [query, setQuery] = React.useState("")
  const [numberType, setNumberType] = React.useState("Standard")
  const [email, setEmail] = React.useState("")
  const [subscribed, setSubscribed] = React.useState(false)

  // 1. The Parse Function Logic
  const subscribeEmail = async (category) => {
    try {
      console.log(`Calling Parse for Category: ${category} with Email: ${email}`);

      Parse.Cloud.run("registerLead", {
        type: category,
        email
      })
      /* Example Parse Call:
         const Subscription = Parse.Object.extend("Subscriptions");
         const sub = new Subscription();
         await sub.save({ email: email, category: category });
      */

      setSubscribed(true);
      setTimeout(() => setSubscribed(false), 4000);
    } catch (error) {
      console.error("Subscription failed", error);
    }
  }

  const handleSearchButton = async () => {
    navigate(`/?q=${query?.trim()}&p=0`)
  }

  return (
    <Layout afterSearch={<></>} >
      <Header />

      <div className="d-xl-flex justify-content-between flex-wrap flex-md-nowrap align-items-center py-4 flex-column text-center" >

        {/* Search Section (Below Subscribe) */}
        <div className="w-100 mt-4" style={{ maxWidth: "600px" }}>
          <Form>
            <Form.Group id="topbarSearch">

              <InputGroup className="input-group-merge search-bar shadow-sm">
                <InputGroup.Text><FontAwesomeIcon icon={faSearch} /></InputGroup.Text>
                <Form.Control
                  size="lg"
                  type="text"
                  name="search"
                  placeholder="Enter number or keyword..."
                  value={query}
                  onChange={(e) => setQuery(e.target.value)}
                />
                <Button onClick={handleSearchButton} variant="primary" className="px-4">
                  SEARCH
                </Button>
              </InputGroup>
            </Form.Group>
          </Form>
        </div>


        <div className="mt-5">
          <NumberFinder onQuery={q => setQuery(q)} />
        </div>
        {/* Subscription Section */}
        <div className="mb-5 w-100 p-4 shadow-sm border rounded bg-light" style={{ maxWidth: "600px" }}>
          <h5 className="mb-3 text-primary">Get Latest Number Updates</h5>
          <Row className="g-3">
            <Col xs={12} md={6}>
              <Form.Group className="text-start">
                <Form.Label className="small fw-bold text-uppercase text-muted">Number Category</Form.Label>
                <InputGroup>
                  <InputGroup.Text><FontAwesomeIcon icon={faLayerGroup} /></InputGroup.Text>
                  <Form.Select
                    value={numberType}
                    onChange={(e) => setNumberType(e.target.value)}
                  >

                    <option value="Feng Shui">Feng Shui Number</option>
                    <option value="Golden">Golden Number</option>
                  </Form.Select>
                </InputGroup>
              </Form.Group>
            </Col>

            <Col xs={12} md={6}>
              <Form.Group className="text-start">
                <Form.Label className="small fw-bold text-uppercase text-muted">Email Address</Form.Label>
                <InputGroup>
                  <InputGroup.Text><FontAwesomeIcon icon={faEnvelope} /></InputGroup.Text>
                  <Form.Control
                    type="email"
                    placeholder="name@example.com"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                  />
                </InputGroup>
              </Form.Group>
            </Col>

            <Col xs={12}>
              {/* Button calls subscribeEmail with numberType parameter */}
              <Button
                variant="secondary"
                className="w-100 fw-bold py-2 shadow-sm"
                onClick={() => subscribeEmail(numberType)}
                disabled={!email}
              >
                <FontAwesomeIcon icon={faBell} className="me-2" />
                SUBSCRIBE FOR DAILY UPDATES
              </Button>
            </Col>
          </Row>

          {subscribed && (
            <Alert variant="success" className="mt-3 mb-0 py-2 small">
              Success! Subscribed to {numberType} updates.
            </Alert>
          )}

          <div className="mt-3">
            <span className="small text-muted">Need Help? </span>
            <a
              href={`https://wa.me/6587413159?text=Send Me Email. My email is: ${email}`}
              style={{ color: '#25D366', fontWeight: 'bold', textDecoration: 'none' }}
            >
              WhatsApp Us Here
            </a>
          </div>
        </div>

        <hr className="w-100" style={{ maxWidth: "600px" }} />
      </div>



    </Layout >
  )
}

export const Head = () => <Seo title="Home" />

export default IndexPage